<template>
	<view class="authCode">
		<view class="title">输入验证码</view>
		<view class="describe">验证码已发送至 +{{ctcode}} {{phone}}</view>
		<u-message-input mode="box" :focus="true" :breathe="true" @finish="finish"></u-message-input>
		<view class="wrap">
			<u-toast ref="uToast"></u-toast>
			<u-verification-code :seconds="seconds" @end="end" @start="start" ref="uCode" @change="codeChange" start-text="重新获取验证码"
			 end-text="重新获取验证码" keep-running  unique-key="page-auth"></u-verification-code>
			<view class="auth_button" @tap="getCode">{{tips}}</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				phone: "",
				ctcode:"",
				tips: '',
				seconds: 60,
			};
		},
		onLoad(options) {
			this.phone = options.phone;
			this.ctcode = options.ctcode;
		},
		onReady() {
			this.$refs.uCode.start();
		},
		methods: {
			finish(e) {
				/* 检查验证码是否正确 */
				this.$API({
					url: `/captcha/verify?phone=${this.phone}&captcha=${e}`
				}).then(res => {
					if(res.data){
						/* 验证成功 */
					}
				})
			},
			codeChange(text) {
				this.tips = text;
			},
			getCode() {
				if (this.$refs.uCode.canGetCode) {
					// 模拟向后端请求验证码
					uni.showLoading({
						title: '正在获取验证码'
					})
					this.$API({
						url: `/captcha/sent?phone=${this.phone}&ctcode=${this.code}`
					}).then(res=>{
						uni.hideLoading();
						// 这里此提示会被this.start()方法中的提示覆盖
						this.$u.toast('验证码已发送');
						// 通知验证码组件内部开始倒计时
						this.$refs.uCode.start();
					})
				} else {
					this.$u.toast('倒计时结束后再发送');
				}
			},
			end() {
				this.$u.toast('倒计时结束');
			},
			start() {
				this.$u.toast('倒计时开始');
			}
		}
	}
</script>

<style lang="scss" scoped>
	.authCode {
		padding: 0 40rpx;
		display: flex;
		flex-direction: column;
		text-align: center;

		.title {
			font-size: 50rpx;
			margin-top: 80rpx;

		}

		.describe {
			margin: 20rpx 0;

		}

		.auth_button {
			margin: 40rpx 0;
			font-size: 34rpx;
			color: #89cc89;
			letter-spacing: 2rpx;
		}
	}

	.wrap {
		padding: 24rpx;
	}
</style>
